.navbar {
    position: fixed;
    top: 0;
    padding: 20px;
    width: 100%;
    height: 30px;
    z-index: 1;
    display: flex;
    justify-content: space-between;
  }
  
  .navbar__black {
    background-color: #111;
  }
  
  .navbar__logo {
    position: fixed;
    left: 20px;
    width: 80px;
    object-fit: contain;
  }
  
  .navbar__avatar {
    position: fixed;
    right: 20px;
    width: 30px;
  }
  