.app {
  background-color: #111;
  color: white;
}

.row {
  margin-left: 20px;
}

.row .title {
  margin-left: 10px;
  font-size: 20px;
  font-weight: bold;
}

.movie-card img {
  width: 100%;
  max-height: 150px;
  object-fit: cover;
  margin-right: 10px;
  transition: transform 0.2s;
}

.movie-card img:hover {
  transform: scale(1.05);
}
